.song-item {
  position: relative;
  width: 400px;
  height: 250px;
  margin: 0 15px 30px 15px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border: 1px solid #16b7cc;
  border-radius: 10px;
  transition: background 0.3s ease;
}
.song-item:hover {
  background: rgba(0, 0, 0, 0.08);
}
.cover {
  width: 100px;
  height: 100px;
}
.track-info {
  display: flex;
  flex-direction: column;
  width: 200px;
}
.play {
  background: url(/itunes-search/tree/master/static/media/play.45971bb9.svg) no-repeat center;
}
.pause {
  background: url(/itunes-search/tree/master/static/media/pause.fea842c0.svg) no-repeat center;
}
.song-icon {
  position: absolute;
  bottom: 30px;
  right: 45px;
  width: 32px;
  height: 32px;
  background-size: contain;
}

.field-wrapper {
  display: flex;
  justify-content: center;
}
.song-field {
  display: flex;
  justify-content: flex-start;
  flex-flow: row wrap;
}

.album-select {
  width: 400px;
}
@media (max-width: 1023px) {
  .album-select {
    width: 300px;
    margin: 30px 0 !important;
  }
}
@media (max-width: 767px) {
  .album-select {
    width: 80%;
  }
}

.input {
  width: 300px !important;
}

@media (max-width: 767px) {
  .input {
    width: 80% !important;
  }
}

.header {
  text-align: center;
  margin-top: 30px;
}
.inputs-wrapper {
  display: flex;
  margin-top: 50px;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 40px;
}
@media (max-width: 1023px) {
  .inputs-wrapper {
    flex-direction: column;
    justify-content: center;
  }
}

